import { useEffect, useState } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { Logo } from '../core/ui';

const logoStyle = {
    width: '26px',
    borderRadius: '50%'
};

const Header = () => {
    const location = useLocation();
    const whitePaths = [
        '/blog',
        '/about'
    ];

    const [scrollTopVal, setScrollTopVal] = useState(0);

    // pc滚动事件触发
    const PC_Scroll = (path: string) => {
        if (whitePaths.includes(path)) setScrollTopVal(1);
        else  setScrollTopVal(0);
        window.removeEventListener('scroll', () => { });
        window.addEventListener('scroll', e => bindHandleScroll(e, path));
    };

    const scrollToTop = () => {
        var c = document.documentElement.scrollTop || document.body.scrollTop;

        if (c > 0) {
            window.requestAnimationFrame(scrollToTop);
            window.scrollTo(0, c - c / 8);
        }
    }

    const bindHandleScroll = (event: any, path: string) => {
        // // 判断用户当前是否进行了横向滚动
        // const scrollLeft =
        //     (event.srcElement
        //         ? event.srcElement.documentElement.scrollLeft
        //         : false) ||
        //     window.pageXOffset ||
        //     (event.srcElement ? event.srcElement.body.scrollLeft : 0);
        // 滚动的高度
        const scrollTop =
            (event.srcElement ? event.srcElement.documentElement.scrollTop : false) ||
            window.pageYOffset ||
            (event.srcElement ? event.srcElement.body.scrollTop : 0);

        if (whitePaths.includes(path)) {
            setScrollTopVal(1);
        } else {
            if (scrollTop < 120) setScrollTopVal(0);
            if (scrollTop >= 120) setScrollTopVal(+(Math.abs(scrollTop / 300).toFixed(1)));
        }
    };
    useEffect(() => {
        scrollToTop();
        const path = location.pathname;
        PC_Scroll(path);
    }, [location]);


    return (
        <header id="masthead" className="site-header" style={{ border: 'none', backgroundColor: `rgba(59,58,54,${scrollTopVal})` }}>
            <nav id="primary-navigation" className="site-navigation">
                <div className="container-fluid">
                    <div className="navbar-header">
                        <a className="site-title yellow-text">
                            <img src={Logo} alt="" style={logoStyle} /> GFE<span className="white-text">-TEAM</span>
                        </a>
                    </div>
                    <div className="main-menu" id="perfect-navbar-collapse">
                        <ul className="nav navbar-nav navbar-right">
                            {/* <li className="active"> */}
                            <li>
                                <Link to="/">首页</Link>
                            </li>
                            <li>
                                <Link to="/blog">文章</Link>
                            </li>
                            <li>
                                <Link to="/about">关于我们</Link>
                            </li>
                            <li>
                                <Link to="/contact">联系我们</Link>
                            </li>
                            {/* <li className="dropdown">
                                <a className="dropdown-toggle">
                                    Blog <i className="fa fa-angle-down hidden-xs" aria-hidden="true"></i>
                                </a>
                                <ul className="dropdown-menu">
                                    <li><a>Blog Post</a></li>
                                </ul>
                            </li> */}
                            {/* <li className="dropdown">
                                <a className="dropdown-toggle">
                                    Portfolio <i className="fa fa-angle-down hidden-xs"></i>
                                </a>
                                <ul className="dropdown-menu">
                                    <li><a>Portfolio Item</a></li>
                                </ul>
                            </li>
                            <li><a>Contact</a></li>
                            <li><a>UI Elements</a></li> */}
                        </ul>
                    </div>
                </div>
            </nav>
        </header>

    );
}

export default Header;